.product-details-2 {
    background: map-get($color, mainBg-1);

    &__section {
        width: 1336px;
    }
    .content {
        border: 1px solid map-get($color, base);
        &__title {
            height: 70px;
            position: relative;
            &::after {
                position: absolute;
                content: "";
                top: 100%;
                left: 0%;
                width: 100%;
                height: 1px;
                background: map-get($color, base);
            }
        }
        &__list {
            display: flex;
            justify-content: space-around;
            padding-top: 24px;
            list-style-type: none;
        }
        &__items {
            & > * {
                @include size(s20);
                @include uppercase;
                color: map-get($color, title);
            }
        }
        

        &__review {
            padding: 0 40px;
            margin-top: 40px;
            height: 400px;
            overflow-y: scroll;
            & > * {
                @include size(s16);
                color: map-get($color, base);
                padding: 8px 0px;
            }

            &--list {
                padding-left: 16px;
            }
            &--items {
                padding: 8px 0px;
            }
        }
        .active {
            & > * {
                color: map-get($color, main);
                }
           
            
        }
    }
}
