.login-register{
    background: #F6F6F6;
    padding: 80px 0;
    &__wrapper{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__title{
        margin-bottom: 40px;
        display: flex;
        justify-content: center;
        width: 100%;
        &--list{
            width: 40%;
            display: flex;
            justify-content: space-around;
            list-style-type: none;
        }
        &--item{
            &>*{
                @include size(s34);
                @include bold;
                color: #757575;
            }
            &:hover{
                cursor: pointer;
            }
        }
    }
    &__form{
        padding: 40px;
        display: flex;
        flex-direction: column;
        background: #fff;
        width: 600px;
        height: 450px;
        &--title{
            margin-bottom: 24px;
            &>*{
                @include size(s18);
                color: map-get($color, title );
            }
        }
    }
    
    &__inputName{
        margin-bottom: 32px;
        width: 100%;
        height: 50px;
        input{
            padding: 0px 8px;
            border: none;
            box-shadow: 0px 3px 6px rgba( #000000, 0.16);
            width: 100%;
            height: 50px;
        }
       
    }
    &__inputPass{
        margin-bottom: 24px;
        width: 100%;
        height: 50px;
        input{
            padding: 0px 8px;
            border: none;
            box-shadow: 0px 3px 6px rgba( #000000, 0.16);
            width: 100%;
            height: 50px;
        }
       
    }
    &__forgetPass {
        width: 100%;
        margin-bottom: 24px;
        display: flex;
        justify-content: center;
        &>*{
            @include size(s16);
            color: map-get($color, title );
        }
    }
    &__button {
        
        display: flex;
        justify-content: center;
        &--submit{
            border: none;
            box-shadow: 0px 3px 6px rgba( #000000, 0.16);
            background: map-get($color , main );
            padding: 24px;
            width: 50%;
            &>*{
                @include size(s18);
                color: map-get($color, mainBg );
            }
        }
    }
    .display-none{
        display: none;
    }
    .active{
        &>*{
            color: #212121;
        }
    }
}