.product-card1{
    padding-top: 80px;
    background: #F6F6F6;
    &__title{
        display: flex;
        flex-direction: column;
        align-items: center;
        
            &>*{
                padding-bottom: 40px;
                @include size(s34);
                @include bold;
                @include uppercase;
                color: map-get($color, title );
            }
    }
    &__content{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }
    &__button{
        width: 100%;
        margin-top: 40px;
        padding-left: 90px;
        @include rp(pl){
            padding-left: 60px;
        }
        @include rp(pl){
            padding-left: 0px;
        }

        display: flex;
        justify-content: flex-start;
        &--back{
            display: flex;
            background: map-get($color , main );
            padding: 24px;
            border: none;
            box-shadow: 0px 3px 6px rgba( #000000,  0.16);
            @include rp(tl){
                width: 100%;
                display: flex;
                justify-content: center;
                padding: 20px;
            }
            
            
            &>*{
                margin-right: 4px;
                @include size(s24);
                color: map-get($color, mainBg );
            }
        }
        &:hover{
            cursor: pointer;
        }
        
    }
    .shop-table{
        border: 1px solid #707070;
        thead{ 
            border: 1px solid #707070;
            &>*{
                height: 100px;
                background: #E8E8E8;
                @include size(s18);
                color: map-get($color, title );
               text-align: center;
            }
        }
        th{
            padding: 8px;
        }
        tbody{
            &>*{
                // height: 100px;
                background: #FFF;
                @include size(s16);
                color: map-get($color, base );
                text-align: center;
                @include rp(mx){
                   font-size: 1.4rem;
                }
            }
            td{
                padding: 24px;
                @include rp(tm){
                 
                    padding: 16px;
                }
                @include rp(ts){
                   
                    padding: 12px;
                }
                @include rp(mx){
                    padding: 8px;
                }
            }
            
        }
        &__contentImg{
            width: 200px;
            @include rp(tx){
                width: 150px;
            }
            @include rp(tm){
                width: 130px;
            }
            @include rp(ts){
                width: 100px;
            }
        }
        &__contentName{
            &>*{
                color: map-get($color , base );
            }
        }
        &__contentQuanlity{
           display: flex;
           justify-content: center;
           border: 1px solid #707070;
           width: 50px;
           &--input{
               border: none;
               width: 100%;
               
           }
           &--button{
               display: flex;
               flex-direction: column;
               padding-left: 16px;
               
            }
            &--add{
                display: flex;
                justify-content: center;
                padding-bottom: 2px;
                border: none;
                &:hover{
                    cursor: pointer;
                }
            }
            &--minus{
                display: flex;
                justify-content: center;
                padding-top: 2px;
                border: none;
                &:hover{
                    cursor: pointer;
                }
            }
           
       }
    }
}