#news-infor-1__wrapper{
    padding: 80px;
    @include rp(pm){
        padding: 70px 0;
    }
    @include rp(ps){
        padding: 60px 0;
    }
    @include rp(tx){
        padding: 50px 0;
    }
    @include rp(tl){
        padding: 40px 0;
    }
    .news-infor__section{
        display: flex;
        margin: 0 -8px;
        @include rp(tm){
            flex-direction: column;
        }
    }
    .news-infor{
        width: 100%;
        &__title{
            display: flex;
            justify-content: center;
            text-align: center;
            &>*{
                @include size(s34);
                @include uppercase;
                @include bold;
                color: map-get($color , title );
            }
        }
        &__infor{
            margin-top: 40px;
            @include rp(pm){
                margin-top: 40px;
            }
            @include rp(ps){
                margin-top: 35px;
            }
            @include rp(tx){
                margin-top: 30px;
            }
            @include rp(tl){
                margin-top: 20px;
            }
            @include rp(tm){
                width: 100%;
                flex: unset;
            }
            padding: 0 8px;
            &--title{
                @include size(s24);
                text-transform: uppercase;
                color: map-get($color, base-2 );
                margin-bottom: 24px;
                @include rp(tm){
                    margin-bottom: 18px;
                }
                @include rp(mx){
                    margin-bottom: 14px;
                }
                text-transform :uppercase;
            }
            &--decs{
                color: map-get($color, sub3 );
                margin-bottom: 24px;
                @include rp(tm){
                    margin-bottom: 18px;
                }
                @include rp(mx){
                    margin-bottom: 14px;
                }
            }
            &--image{
                color: map-get($color, sub3 );
                padding: 40px 8px;
                @include rp(tm){
                    padding: 30px 8px;
                }
                @include rp(mx){
                    padding: 20px 8px;
                }
                display: flex;
                @media (max-width: 640px) {
                    flex-direction: column;
                }
                img{
                    @media (max-width: 640px) {
                        margin-bottom: 40px;
                    }
                }
            }
        }
        &__news--mobile{
            padding: 0 8px;
            width: 100%;
            display: none;
            @include rp(tm){
                display: block;
                margin-bottom: 40px;
            }
            &--items{
                height: 368px;
                @include rp(ps){
                    height: 306px;
                }
                @include rp(ts){
                    height: 40vw;
                }
                @include rp(ml){
                    height: 45vw;
                }
                .items{
                    height: 100%;
                    position: relative;
                    &__image{
                        height: 100%;
                    }
                    &__infor{
                        transition: .3s ease-in-out;
                        background: map-get($color, underline );
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        padding: 16px 26px;
                        @include rp(mx){
                            padding: 10px 16px;
                        }
                        &--title{
                            @include p-line-clamp(2);
                            @include size(s20);
                            color: map-get($color, base-2 );
                        }
                    }
                }
            }
            
        }
        &__news{
            margin-top: 40px;
            @include rp(pm){
                margin-top: 40px;
            }
            @include rp(ps){
                margin-top: 35px;
            }
            @include rp(tx){
                margin-top: 30px;
            }
            @include rp(tl){
                margin-top: 20px;
            }
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            @include rp(tm){
                display: none;
            }
            margin-left: auto;
            padding: 0 8px;
            &--items{
                margin-bottom: 24px;
                @include rp(tm){
                    margin-bottom: 18px;
                }
                @include rp(mx){
                    margin-bottom: 14px;
                }
                height: 368px;
                @include rp(ps){
                    height: 306px;
                }
                @include rp(ts){
                    height: 40vw;
                }
                @include rp(ml){
                    height: 45vw;
                }
                .items{
                    height: 100%;
                    position: relative;
                    &__image{
                        height: 100%;
                    }
                    &__infor{
                        transition: .3s ease-in-out;
                        background: map-get($color, underline);
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        padding: 16px 26px;
                        @include rp(mx){
                            padding: 10px 16px;
                        }
                        &--title{
                            @include p-line-clamp(2);
                            @include size(s20);
                            color: map-get($color, base-2 );
                        }
                    }
                }
            }
        }
    
    }
}