.index2{
    @include spaceIndex2(s80, margin-top);
    .index-service{
        background: map-get($color, main-2 );
        &__title{
            @include spaceIndex2(s80, padding-top);
            text-align: center;
            &>*{
                @include size(s40);
                @include bold;
                color: map-get($color, mainBg );
            }
        }
        .box{
            width: 100%;
            @include rp(tm){
                display: flex;
                justify-content: center;


            }
            @include spaceIndex2(s80, margin-bottom);
            @include spaceIndex2(s80, margin-top);
            &__content{
                background: map-get($color, mainBgOverlay );
                @include blockIndex(widthBoxIndex2,width);
                display: flex;
                flex-direction: column;
                align-items: center;
                @include spaceIndex2(s140, padding-left);
                @include spaceIndex2(s140, padding-right);
                @include spaceIndex2(s32, padding-top);
                @include spaceIndex2(s32, padding-bottom);
                &:hover{
                    background: map-get($color, main )
                }
            }
            &__img{
                @include blockIndex(imgIndex2Width,width);
                @include blockIndex(imgIndex2Height,height);
                
            }
            &__title{
                @include spaceIndex2(s32, padding-bottom);
                @include spaceIndex2(s32, padding-top);
                &>*{
                    @include size(s24);
                    @include rp(ts){
                        font-size: 2rem;
                    }
                    @include bold;
                    color: map-get($color , mainBg );
                }
            }
            &__desc{
                @include spaceIndex2(s32, padding-bottom);
                @include size(s18);
                @include rp(ts){
                    font-size: 1.6rem;
                }
                color: map-get($color , mainBg );
                
            }
            &__more{
                &>*{
                    color: map-get($color, mainBg );
                }
                svg{
                    margin-left: 8px;
                }
            }
        }
       
    }
}