abbr[title],
abbr[data-original-title] {
	text-decoration: underline;
	-webkit-text-decoration: underline dotted;
	text-decoration: underline dotted;
	cursor: help;
	border-bottom: 0;
	-webkit-text-decoration-skip-ink: none;
	text-decoration-skip-ink: none;
}

address {
	margin-bottom: 0;
	font-style: normal;
	line-height: inherit;
}

// ol,
// ul,
// dl {
// 	margin-top: 0;
// 	margin-bottom: 0;
// }

// ol ol,
// ul ul,
// ol ul,
// ul ol {
// 	margin-bottom: 0;
