#contact-1__wrapper {
    background: #f6f6f6;
    padding: 80px 0;
    display: flex;
    width: 100%;
    .product {
        width: 100%;
        &__title {
            display: flex;
            justify-content: center;
            margin-bottom: 40px;
            & > * {
                @include size(s34);
                color: map-get($color, title);
                @include uppercase;
            }
        }
        .content {
            width: 100%;
            padding: 0 8px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            // height: 100%;
            @media (max-width: 640px) {
                margin-bottom: 40px;
                height: unset;
            }
            flex: unset;
            &__section {
                display: flex;
                align-items: center;
                height: 100%;
                width: 100%;
                justify-content: space-between;
                // @include blockAboutUs(AboutUs, height);
                transition: 0.2s ease-in-out;
                @media (max-width: 640px) {
                    width: 100%;
                    flex-direction: column;
                }
            }
            &__header {
                margin-bottom: 24px;
                @include rp(tx) {
                    margin-bottom: 20px;
                }
                & > * {
                    @include uppercase;
                    @include size(s24);
                    color: map-get($color, title);
                    @media (max-width: 640px) {
                        margin-bottom: 40px;
                    }
                }
            }
            &__items {
                margin-bottom: 24px;
                @include rp(tx) {
                    margin-bottom: 20px;
                }
                &--title {
                    @include size(s20);
                    color: map-get($color, title);
                    margin-bottom: 24px;
                    @include rp(tx) {
                        margin-bottom: 20px;
                    }
                }
                &--decs:not(:last-child) {
                    margin-bottom: 24px;
                    @include rp(tx) {
                        margin-bottom: 20px;
                    }
                }
                &--decs {
                    color: #9e9e9e;
                    @media (max-width: 640px) {
                        margin-bottom: 24px;
                        @include rp(tx) {
                            margin-bottom: 20px;
                        }
                    }

                    p span {
                        color: #9e9e9e;
                    }
                }
            }
        }
    }
    .contact-1__section {
        display: flex;
        @include rp(ts){
            flex-direction: column;
        }
        &--form{
            
            padding: 40px;
            display: flex;
            flex-direction: column;
            background: #fff;
            width: 600px;
            @include rp(ts){
                width: 100%;
            }
            height: 450px;
            &--title{
                margin-bottom: 24px;
                &>*{
                    @include size(s18);
                    color: map-get($color, title );
                }
            }
        }
        .contact-form{
            &__name{
                margin-bottom: 24px;
                width: 100%;
                height: 50px;
                input{
                    padding: 0px 8px;
                    border: none;
                    box-shadow: 0px 3px 6px rgba( #000000, 0.16);
                    width: 100%;
                    height: 50px;
                }
               
            }
            &__phone{
                margin-bottom: 24px;
                width: 100%;
                height: 50px;
                input{
                    padding: 0px 8px;
                    border: none;
                    box-shadow: 0px 3px 6px rgba( #000000, 0.16);
                    width: 100%;
                    height: 50px;
                }
               
            }
            &__email{
                margin-bottom: 24px;
                width: 100%;
                height: 50px;
                input{
                    padding: 0px 8px;
                    border: none;
                    box-shadow: 0px 3px 6px rgba( #000000, 0.16);
                    width: 100%;
                    height: 50px;
                }
               
            }
            &__unit{
                margin-bottom: 24px;
                width: 100%;
                height: 50px;
                input{
                    padding: 0px 8px;
                    border: none;
                    box-shadow: 0px 3px 6px rgba( #000000, 0.16);
                    width: 100%;
                    height: 50px;
                }
               
            }
            &__button{
                display: flex;
                justify-content: space-around;
                .btn--link{
                    border: none;
                    width: 200px;
                    padding: 24px;
                    background: map-get($color , main );
                    &>*{
                        color: #fff;
                        font-size: 1.8rem;
                    }
                    &:hover{
                        cursor: pointer;
                    }
                }
            }
           
        }
        
    }
}
