#header {
  position: sticky;
  top: 0;
  z-index: 5000;
}
.container{
  display: flex;
}
.header {
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.16);
  &-top {
    @include blockHeader(top, height);
    background-color: map-get($color, main);

    .container {
      height: 100%;
    }

    &__content {
      width: 100%;
      height: 100%;

      .content {
        height: 100%;

        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;

      }
      .content-search{
        display: flex;
        @include blockHeader(searchWitdth, width);
        &--input{
          @include spaceHeader(s16,padding-left);
          width: 80%;
          @include rp(mx){
            width: 70%;
          }
          @include blockHeader(searchHeight,height);
          border-top-left-radius: 10px;
          border-bottom-left-radius: 10px;
          border: none;
        }
        &--button{
          width: 20%;
          @include rp(mx){
            width: 30%;
          }
          @include blockHeader(searchHeight,height);
          border-top-right-radius: 10px;
          border-bottom-right-radius: 10px;
          border: none;
          background: map-get($color, buttonSearch );
          &>*{
            @include size(s18);
            @include rp(mx){
              font-size:  1.4rem;
            }
            @include bold;
            color: map-get($color, mainBg );
          }
        }
      }
      .content-info{
        display: flex;
        &__phone{
          @include spaceHeader(s16, margin-right);
          @include rp(tx){
            display: none;
          }
          &>*{
            @include size(s18);
            @include rp(ps){
              font-size: 1.7rem;
            }
            
            @include bold;
            color: map-get($color, mainBg );
            span{
              margin-left: 8px;
            }
          }
        }
        &__account{
          display: flex;
          &>*{
            @include size(s18);
            @include rp(ps){
              font-size: 1.7rem;
            }
            @include bold;
            color: map-get($color, mainBg );
          }
        }
        &__login{
          margin-right: 8px;
          &>*{
            color: #fff
          }
        }
        &__register{
          &>*{
            color: #fff
          }
        }
        
      }
    }
  }

  &-main{
    @include blockHeader(main, height);
    background: map-get($color, mainBg );
    position: relative;
    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 100%;

      @include rp(tl){
        position: initial;
      }
    }
    &__logo{
      display: block;
      // width: 186px;
      @include blockHeader(logoWidth, width);

      height: 100%;
      img {
        object-fit: contain;
        object-position: left;
      }
    }
    &__nav{
      
      list-style-type: none;
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: space-around;
      align-items: center;
      @include rp(tx) {
        position: absolute;
        top: calc(100% - 1px);
        right: 0;

        height: auto;
        
        // display: flex;
        justify-content: initial;
        align-items: center;
        flex-direction: column;
        background: map-get($color, mainBg );
        box-shadow: 0px 3px 6px rgba(#000, .16);
        width: 30%;
        display: none;

        &.active{
          display: inherit;
          animation: fadeInUp .16s ease forwards;
        }

        min-width: 300px;
        @include rp(sl){
          min-width: 100%;
          width: 100%;
          max-width: 100%;
        }

        background-color: map-get($color, mainBg);
        box-shadow: 0 3px 3px rgba(0, 0, 0, 0.16);

        max-height: calc(100vh - 320px);
        overflow-y: auto;
      }
      &-item{
        position: relative;
        height: 123px;
        display: flex;
        justify-content: center;
        align-items: center;
        @include spaceHeader(s42, margin-left);
        @include rp(tx){
          @include spaceHeader(s16 ,margin, 0);
        }
        &>*{
          @include size(s18);
          @include bold;
          color: map-get($color, base );
        }
      }
      
      
    }
    &__listService{
      display: none;
      background: map-get($color, mainBg );
      list-style-type: none;
      position: absolute;
      top: 123px;
      left: 0;
      padding: 16px;
      z-index: 1;
      width: 250px;
      height: 100px;
      transform: translateY(calc(100%-123px));
      &--items{
        display: flex;
        justify-content: center;
        padding-bottom: 8px;
        margin: 8px;
        position: relative;
        &:not(:nth-child(2n)):after{
          position: absolute;
          content: "";
          top: 100%;
          left: 0;
          width: 100%;
          height: 1px;
          background: #757575;

        }
        &>*{
          @include size(s18);
          color: map-get($color, base );
          @include uppercase;
        }
      }
    }
    .hover{
      &:hover{
        .header-main__listService{
          display: block;
        }
      }
    }
    &__mobile{
      width: 10%;
      @include rp(mx){
        width: 20%;
      }
      display: flex;
      justify-content: space-between;
    }
    &__cart{
      height: auto;
      display: flex;
      align-items: center;
      img {
        width: 32px;
        height: 32px;
        object-fit: contain;
        object-position: right;
      }
    }
    &__button {
      width: 42px;
      height: auto;
      border-radius: 3px;
      // background-color: rgba(map-get($color, base), 0.4);
      
      display: flex;
      justify-content: center;
      align-items: center;

      cursor: pointer;

      @include rp(tx) {
        display: inherit;
      }

      &-border {

        width: 80%;
        height: 2px;
        border-radius: 1px;
        background-color: map-get($color, main);
        transition: 0.3s all ease;

        position: relative;
        &::before,
        &::after {
          content: "";
          position: absolute;
          top: -12px;
          left: 0;
          bottom: initial;
          right: initial;
          width: 100%;
          height: 2px;
          background-color: map-get($color, main);

          transition: 0.3s all ease;
        }

        &::after {
          top: initial;
          bottom: -12px;
        }
      }
      display: none;
      &.show {
        display: inherit;
      }
      &.active {
        .header-main__button-border {
          height: 0;
          background-color: transparent;
         
          &::before,
          &::after {
            top: 50%;
            bottom: initial;
            width: 100%;
          }
          &::before {
            transform: translateY(-50%) rotate(45deg);
          }
          &::after {
            transform: translateY(-50%) rotate(-45deg);
          }
        }
      }
    }
  }
}