.intro-3 {
    .content {
        padding: 0 8px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        // height: 100%;
        @media (max-width: 640px) {
            margin-bottom: 40px;
            height: unset;
        }
        flex: unset;
        &__section {
            display: flex;
            align-items: center;
            height: 100%;
            width: 100%;
            justify-content: space-between;
            // @include blockAboutUs(AboutUs, height);
            transition: 0.2s ease-in-out;
            @media (max-width: 640px) {
                width: 100%;
                flex-direction: column;
            }
        }
        &__header {
            margin-bottom: 24px;
            @include rp(tx){
                margin-bottom: 20px;
            }
            & > * {
                @include uppercase;
                @include size(s24);
                color: map-get($color, title);
                @media (max-width: 640px) {
                    margin-bottom: 40px;
                }
            }
        }
        &__items {
            margin-bottom: 24px;
            @include rp(tx){
                margin-bottom: 20px;
            }
            &--title {
                @include size(s20);
                color: map-get($color, title);
                margin-bottom: 24px;
                @include rp(tx){
                    margin-bottom: 20px;
                }
            }
            &--decs:not(:last-child) {
                margin-bottom: 24px;
                @include rp(tx){
                    margin-bottom: 20px;
                }
            }
            &--decs {
                color: #9e9e9e;
                @media (max-width: 640px) {
                    margin-bottom: 24px;
                    @include rp(tx){
                        margin-bottom: 20px;
                    }
                }

                p span {
                    color: #9e9e9e;
                }
            }
        }
    }
}
