.forget-password{
    background: #F6F6F6;
    padding: 80px 0;
    &__wrapper{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__desc{
        width: 100%;
        margin-bottom: 24px;
        &>*{
            @include size(s16);
            color: map-get($color , title );
        }
    }
    &__form{
        padding: 40px;
        display: flex;
        flex-direction: column;
        background: #fff;
        width: 550px;
        height: 350px;
        &--title{
            margin-bottom: 24px;
            &>*{
                @include size(s18);
                color: map-get($color, title );
            }
        }
    }
    &__inputName{
        margin-bottom: 32px;
        width: 100%;
        height: 50px;
        input{
            padding: 0px 8px;
            border: none;
            box-shadow: 0px 3px 6px rgba( #000000, 0.16);
            width: 100%;
            height: 50px;
        }
       
    }
    &__button {
        
        display: flex;
        justify-content: center;
        &--submit{
            border: none;
            box-shadow: 0px 3px 6px rgba( #000000, 0.16);
            background: map-get($color , main );
            padding: 24px;
            width: 50%;
            &>*{
                @include size(s18);
                color: map-get($color, mainBg );
            }
        }
    }
}