.index3 {
    .index-aboutUs {
        @include spaceIndex1(s80, margin-top);
        @include spaceIndex1(s80, margin-bottom);
        .content {
            
            display: flex;
            @include rp(tm){
                display: flex;
                flex-direction: column;
                align-items: center;
            }
            &__left {
                width: 50%;
                @include rp(tm){
                    width: 80%;
                }
                @include spaceIndex3(s108, padding-right);
               
            }
            &__title{
                @include spaceIndex3(s24, margin-bottom);
                &>*{
                    @include size(s40);
                    @include rp(tx){
                        font-size: 3.4rem;;
                    }
                    @include bold;
                    
                }
            }
            &__desc{
                
                &>*{
                    @include size(s18);
                    @include rp(pm){
                        font-size: 1.6rem;;
                    }
                    @include rp(tx){
                        font-size: 1.4rem;;
                    }
                    color: map-get($color, base-2 );
                    line-height: 1.6;
                }
            }
            &__more{
                display: flex;
                justify-content: center;
                @include spaceIndex3(s24, margin-top);
                @include blockIndex(widthMoreIndex3, width); 
                padding: 12px;
                background: map-get($color, buttonSearch );
                border-radius: 16px;
                &>*{
                    @include size(s16);
                    @include rp(tm){
                        font-size: 1.4rem;
                    }
                    color: map-get($color, mainBg );
                }
                svg{
                    margin-left: 8px,;
                }
            }
            &__right {
                width: 50%;
                @include rp(tm){
                    width: 80%;
                    margin-top: 16px;
                }
               position: relative;
            }
            &__img {
                @include blockIndex(imgIndex3Width, width);
                @include blockIndex(imgIndex3Height, height);
               
            }
            &__overlay{
                @include blockIndex(imgIndex3Width, width);
                @include blockIndex(imgIndex3Height, height);
                background: map-get($color, main-2 );
                position: absolute;
                top: 5%;
                left: 5%;
                z-index: -1;
            }
        }
    }
}
