#footer {
    @include footerBox(s60, padding);
    background: map-get($color, footer-bg);
    .footer_wrapper {
        @include rp(tx){
            width:85%;
            flex: 0 0 85%;
        }
        @include rp(tm){
           flex-direction: column;
        }
        margin: auto;
        
        .footer__more{
            &--pic{
                @include rp(pm){
                    width: 28px;
                }
                @include rp(tx){
                    width: 20px;
                }
                @include rp(tm){
                    display: none;
                }
                display: flex;
                margin-top: 8px;
                img{
                    margin-left: 4px;
                }
            }
        }
        .footer-1 {
            @include footerBox(s100, padding-right);
        }
        .footer-2{
            &__link{
                @include footerBox(s100, padding-right);
                @include rp(tm){
                    padding-top: 16px;
                }
                ul{
                    list-style-type: none;
                }
                .items{
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    padding-bottom: 32px;
                    &>*{
                        @include size(s18);
                        @include rp(tx){
                            font-size: 1.6rem;
                        }
                        @include bold;
                        color: map-get($color, title );
                    }
                }
                
            }
        }
        .footer-3{
            @include footerBox(s100, padding-right);
            @include rp(tm){
                padding-top: 16px;
                padding-bottom: 16px;
            }
            &__title{
                &>*{
                    @include size(s18);
                    @include rp(tx){
                        font-size: 1.6rem;
                    }
                    @include bold;
                    color: map-get($color, title );
                }
            }
            &__infor{
                padding-top: 16px;
                &--items{
                    padding-top: 16px;
                    &>*{
                        @include size(s16);
                        @include rp(tx){
                            font-size: 1.4rem;
                        }
                        color: map-get($color, base );
                    }
                }
            }
        }
    }
}
