.index{
    .index-product{
        @include spaceIndex1(s80, margin-top);
        .content{
            width: 100%;
            &__title{
                display: flex;
                justify-content: center;
                @include spaceIndex1(s40, margin-bottom);
                    &>*{
                        @include size(s40);
                        color: map-get($color, base );
                    }
            }
            &__titleBox{
                &--list{
                    height: 40px;
                    list-style-type: none;
                    display: flex;
                    justify-content: center;
                    position: relative;
                    &::after{
                        position: absolute;
                        content: "";
                        top: 100%;
                        left: 0%;
                        width: 100%;
                        height: 1px;
                        background: map-get($color, underline );
                    }
                   
                }
                &--items{
                    // @include spaceIndex1(s72, margin-left);
                    // @include spaceIndex1(s72, margin-right);
                    display: flex;
                    width: 100%;
                    justify-content: space-around;
                   
                    &>*{
                        @include size(s24);
                        @include rp(pm) {
                            font-size: 2.2rem;
                        }
                        @include rp(tx) {
                            font-size: 2rem;
                        }
                        @include bold;
                        color: map-get($color, base-2 );
                    }
                    
                }
            }
            &__product{
                display: none;
            }
            &__box{
                
                .box{
                    &:hover{
                        background: map-get($color, boxOverlay);
                        transition: all 0.3s ease-in-out;
                    }
                    box-shadow: 0px 3px 6px rgba(#000, .16);
                    @include spaceIndex1(s64, margin-top);
                    &__img{
                        @include blockIndex(imgIndex1Width,width);
                        @include blockIndex(imgIndex1Height,height);
                    }
                    &__content{
                        @include spaceIndex1(s64, padding-left);
                        @include spaceIndex1(s64, padding-right);
                    }
                    &__title{
                        @include spaceIndex1(s32, padding-top);
                        @include spaceIndex1(s24, margin-bottom);
                        text-align: center;
                        &>*{
                            @include size(s24);
                            @include rp(tx) {
                                font-size: 2rem;
                            }
                            @include rp(tm) {
                                font-size: 1.7rem;
                            }
                            @include bold;
                            color: map-get($color, base );
                        }
                    }
                    &__titleSub{
                        @include spaceIndex1(s24, margin-bottom);
                        text-align: center;
                        &>*{
                            @include size(s20);
                            @include rp(tx) {
                                font-size: 1.8rem;
                            }
                            @include rp(tm) {
                                font-size: 1.6rem;
                            }
                            @include bold;
                            color: map-get($color, base-2 );
                        }
                    }
                    &__price{
                        @include spaceIndex1(s24, margin-bottom);
                        text-align: center;
                        &>*{
                            @include size(s18);
                            @include rp(tx) {
                                font-size: 1.6rem;
                            }
                            @include rp(tm) {
                                font-size: 1.4rem;
                            }
                            @include bold;
                            color: map-get($color, base-2 );
                        }
                    }
                    &__priceDiscount{
                        @include spaceIndex1(s24, margin-bottom);
                        text-align: center;
                        &>*{
                            @include size(s24);
                            @include rp(tx) {
                                font-size: 2rem;
                            }
                            @include rp(tm) {
                                font-size: 1.8rem;
                            }
                            @include bold;
                            color: map-get($color, priceDiscount );
                        }
                    }
                    &__more{
                        @include spaceIndex1(s32, padding-bottom);
                        display: flex;
                        justify-content: center;
                        svg{
                            margin-left: 8px;
                        }
                    }
                }
            }
            .content--center{
                display: flex;
                justify-content: center;
            }
            &__more{
                @include spaceIndex1(s64, margin-top);
                display: flex;
                @include blockIndex(widthMoreIndex1, width);
                justify-content: center;  
                padding: 12px;
                background: map-get($color, buttonSearch );
                border-radius: 16px;
                &>*{
                    @include size(s16);
                    @include rp(tm){
                        font-size: 1.2rem;
                    }
                    color: map-get($color, mainBg );
                }
                svg{
                    margin-left: 8px,;
                }
            }
            .active{
                position: relative;
                &::after{   
                    position: absolute;
                    content: "";
                    top: 100%;
                    left: 0%;
                    width: 100%;
                    z-index: 1;
                    height: 4px;
                    background: map-get($color, buttonSearch );
                }
                &>*{
                    color: map-get($color, buttonSearch );
                }
            }

        }
    }
}