.intro-2{
    
    .content{
        width: 100%;
        &__title{
            margin-bottom: 24px;
            @include rp(tx){
                margin-bottom: 20px;
            }
            &>*{
                @include uppercase;
                @include size(s24);
                color: #757575;
            }
            
        }
        &__desc{
            margin-bottom: 24px;
            @include rp(tx){
                margin-bottom: 20px;
            }
            &>*{
                @include size(s16);
                color: #9E9E9E;
            }
        }
        &__img{
            margin-bottom: 24px;
            @include rp(tx){
                margin-bottom: 20px;
            }
            display: flex;
            justify-content: space-between;
            &--left{
                width: 600px;
                height: 500px;
                @include rp(pm){
                    width: 500px;
                    height: 450px;
                }
                @include rp(ps){
                    width: 480px;
                    height: 430px;
                }
                @include rp(tx){
                    width: 450px;
                    height: 400px;
                }
                @include rp(tl){
                    width: 430px;
                    height: 380px;
                }
                @include rp(tm){
                    width: 380px;
                    height: 350px;
                }
                @include rp(ts){
                    width: 330px;
                    height: 300px;
                }
                img{
                    object-fit: cover;
                }
            }
            &--right{
                width: 600px;
                height: 500px;
                @include rp(pm){
                    width: 500px;
                    height: 450px;
                }
                @include rp(ps){
                    width: 480px;
                    height: 430px;
                }
                @include rp(tx){
                    width: 450px;
                    height: 400px;
                }
                @include rp(tl){
                    width: 430px;
                    height: 380px;
                }
                @include rp(tm){
                    width: 380px;
                    height: 350px;
                }
                @include rp(ts){
                    width: 330px;
                    height: 300px;
                }
                @include rp(mx){
                    width: 330px;
                    height: 300px;
                }
                img{
                    object-fit: cover;
                }
            }
        }
    }
    
}