.intro-1{
    padding-top: 80px;
    @include rp(pl){
        padding-top: 70px ;
    }
    @include rp(pm){
        padding-top: 60px ;
    }
    @include rp(ps){
        padding-top: 50px ;
    }
    @include rp(tx){
        padding-top: 40px ;
    }
    margin: auto;
    &__title{
        display: flex;
        justify-content: center;
        margin-bottom: 32px;
        @include rp(tx){
            margin-bottom: 24px;
        }
        &>*{
            @include uppercase;
            @include size(s34);
            @include rp(tx){
                font-size: 3rem;
            }
            color: map-get($color, title);
        }
    }
    .content{
        &__title{
            margin-bottom: 24px;
            @include rp(tx){
                margin-bottom: 20px;
            }
            &>*{
                @include uppercase;
                @include size(s24);
                @include rp(tx){
                   font-size: 20px;
                }
                color: #757575
            }
        }
        &__desc{
            margin-bottom: 32px;
            @include rp(tx){
                margin-bottom: 24px;
            }
            &>*{
                @include size(s16);
                color: #9E9E9E;
            }
        }
        &__img{
            margin-bottom: 32px;
            width: 100%;
            height: 600px;
            @include rp(pm){
                height: 550px;
            }
            @include rp(tx){
                height: 500px;
            }
            @include rp(tl){
                height: 480px;
            }
            @include rp(tm){
                height: 450px;
            }
            @include rp(ts){
                height: 420px;
            }
            @include rp(mx){
                height: 400px;
            }

           
            img{
                object-fit: cover;
            }
        }
    }
}