.product-details-3{
    background: map-get($color, mainBg-1);
    
    .product-details__title{
        display: flex;
        justify-content: center;
        padding-top: 40px;
        padding-bottom: 8px;
        &>*{
            @include size(s24);
            @include uppercase;
            color: map-get($color, base );
        }
    }
    .product-details__slider{
        position: relative;
    $wh: 43px;
    .swiper-button-next,
    .swiper-button-prev{
      &::after{
        content: "";
      }
      background-color: map-get($color, main);
      

      width: $wh;
      height: $wh;

      @media (max-width: map-get($media, tl)){
        $wh: 36px;

        width: $wh;
        height: $wh;
      }

      @media (max-width: map-get($media, mx)){
        $wh: 30px;

        width: $wh;
        height: $wh;
      }

      border-radius: 1000px;
      @include rp(ts){
        svg{
            width: 10px;
            height: 10px;
        }
    }
    }
    .swiper-button-next{
        right: -50px;
        @include rp(tl){
            right: -15px;
        }
        @include rp(tm){
            right: -10px;
        }
        
    }
    .swiper-button-prev{
        left: -50px;
        @include rp(tl){
            left: -15px;
        }
    
        @include rp(tm){
            left: -10px;
        }
    }
    }
    .box {
        width: 200px;
        padding-bottom: 40px;
        &__img {
            height: 150px;
            @include rp(pm) {
                height: 115px;
            }
        }
        &__content {
            background: map-get($color, mainBg);
            padding: 18px;
            @include rp(pm) {
                padding: 14px;
            }
            position: relative;
            &::after {
                position: absolute;
                content: "";
                top: 100%;
                left: 0%;
                width: 100%;
                height: 2px;
                background: map-get($color, main);
            }
            &:hover {
                background: map-get($color, boxOverlay);
            }
        }
        &__title {
            margin-bottom: 20px;
            @include rp(pm) {
                margin-bottom: 16px;
            }
            & > * {
                @include size(s16);
                color: map-get($color, base-2);
            }
        }
        &__priceDiscount {
            margin-bottom: 20px;
            @include rp(pm) {
                margin-bottom: 16px;
            }
            & > * {
                @include size(s14);
                color: map-get($color, base-2);
            }
        }
        &__more {
            display: flex;
            justify-content: space-between;

            svg {
                margin-left: 4px;
            }
        }
    }
}