.news-1 {
    background: #f6f6f6;
    padding: 80px 0;
    @include rp(pm){
        padding: 70px 0;
    }
    @include rp(ps){
        padding: 60px 0;
    }
    @include rp(tx){
        padding: 50px 0;
    }
    @include rp(tl){
        padding: 40px 0;
    }
    &__title {
        margin-bottom: 40px;
        @include rp(pm){
            margin-bottom: 32px;
        }
        @include rp(ps){
            margin-bottom: 28px;
        }
        @include rp(tx){
            margin-bottom: 24px;
        }
        
        display: flex;
        justify-content: center;
       &>*{
           @include size(s34);
           @include rp(pm){
            font-size: 3.2rem;
        }
           @include uppercase;
           color: map-get($color , title );
       }
    }
    .news-box {
        position: relative;
        background: map-get($color, mainBg);
        &--padding {
            padding: 0px 8px;
        }
        &__img {
           width: 100%;
           height: 200px;
        }
        &__content {
            padding-left: 24px;
            padding-right: 24px;
            margin-top: 24px;
            @include rp(pm){
                padding-left: 20px;
                padding-right: 20px;
                margin-top: 20px;
            }
            @include rp(tx){
                padding-left: 16px;
                padding-right: 16px;
                margin-top: 16px;
            }
            
        }
        &__time {
            margin-bottom: 24px;
            @include rp(pm){
                margin-bottom: 20px;
            }
            @include rp(tx){
                margin-bottom: 16px;
            }
            & > * {
                @include size(s16);
                @include rp(pm){
                    font-size: 1.4rem;
                }
                color: map-get($color, main-2);
            }
        }
        &__title {
            margin-bottom: 24px;
            @include rp(pm){
                margin-bottom: 20px;
            }
            @include rp(tx){
                margin-bottom: 16px;
            }
            & > * {
                @include size(s24);
                @include rp(pm){
                    font-size: 2rem;
                }
                color: map-get($color, main-2);
            }
        }
        &__desc {
            margin-bottom: 24px;
            @include rp(pm){
                margin-bottom: 20px;
            }
            @include rp(tx){
                margin-bottom: 16px;
            }
            & > * {
                @include size(s16);
                @include rp(pm){
                    font-size: 1.4rem;
                }
                line-height: 1.4;
                color: map-get($color, base-2);
            }
        }
        &__more {
            padding-bottom: 24px;
            @include rp(pm){
                margin-bottom: 20px;
            }
            @include rp(tx){
                margin-bottom: 16px;
            }
        }
    }
    .box-pagination{
        margin-top: 40px;
        @include rp(pm){
            margin-bottom: 32px;
        }
        @include rp(tx){
            margin-bottom: 28px;
        }
    }
}
