$spaceHeader: (
  s16: (
    default: 16px,
  ),
	s42: (
    default: 42px,
    pl: 28px,
    pm: 14px,
  ),
);

$blockHeader: (
	top: (
    default: 80px,
    ps: 52px,
    tl: 48px,
    ts: 42px,
  ),
  searchWitdth: (
    default: 700px,
    pl: 600px,
    pm: 500px,
    tx: 500px,
    tl: 450px,
    ts: 400px,
    mx: 350px,
    ml: 300px,
    mm: 250px,
    ms: 200px,
  ),
  searchHeight: (
    default: 40px,
    pl: 30px,
  ),
  main: (
    default: 123px,
    ps: 110px,
    tl: 90px,
    ts: 80px,
    ml: 72px,
  ),
  logoWidth: (
    defalut: 186px,
    pl: 172px,
    pm: 150px,
  ),
);

@mixin blockHeader($key, $name, $side: '-1'){
  @each $idx, $val in map-get($blockHeader, #{$key}){
    @if $side == '-1'{
      @if $idx == default{
        #{$name}: $val;
      }
      @else{
        @media (max-width: map-get($media, $idx)){
          #{$name}: $val;
        }
      }
    }
    @else{
      @if $idx == default{
        #{$name}: $val $side;
      }
      @else{
        @media (max-width: map-get($media, $idx)){
          #{$name}: $val $side;
        }
      }
    }
  }
}

@mixin spaceHeader($key, $name, $side: '-1'){
  @each $idx, $val in map-get($spaceHeader, #{$key}){
    @if $side == '-1'{
      @if $idx == default{
        #{$name}: $val;
      }
      @else{
        @media (max-width: map-get($media, $idx)){
          #{$name}: $val;
        }
      }
    }
    @else{
      @if $idx == default{
        #{$name}: $val $side;
      }
      @else{
        @media (max-width: map-get($media, $idx)){
          #{$name}: $val $side;
        }
      }
    }
  }
}