.product-1 {
    background: map-get($color, mainBg-1);

    .product {
        &__title {
            display: flex;
            justify-content: center;
            margin-bottom: 40px;
            @include rp(pm){
                margin-bottom: 28px;
            }
            @include rp(tm){
                margin-bottom: 24px;
            }
            & > * {
                @include size(s34);
                @include rp(tx){
                    font-size: 3rem;
                }
                color: map-get($color, title);
                @include uppercase;
                @include bold;
            }
        }

        &__section {
            width: 100%;
            display: flex;
        }
        &__options {
            width: 30%;
        }
        &__list {
            background: map-get($color, mainBg);
            list-style-type: none;
            padding: 24px 36px;
            @include rp(pm){
                padding: 24px 32px;
            }
            @include rp(tx){
                padding: 18px 24px;
            }
        }
        &__items {
            padding-bottom: 16px;
            margin-bottom: 16px;
            @include rp(tx){
                padding-bottom: 12px;
                margin-bottom: 12px;
            }
            position: relative;
            &::after {
                position: absolute;
                top: 100%;
                left: 50%;
                height: 1px;
                width: 100%;
                content: "";
                transform: translateX(-50%);
                background: map-get($color, underline);
            }
            & > * {
                @include size(s20);
                @include rp(tx){
                    font-size: 1.8rem;
                }
                @include rp(tm){
                    font-size: 1.6rem;
                }
                @include uppercase;
                color: map-get($color, main-2);
            }
        }
        &__content {
            width: 70%;
            .box-pagination {
                margin-top: -20px;
                padding-bottom: 20px;
                @include rp(pm) {
                    margin-top: -14px;
                    padding-bottom: 14px;
                }
            }
        }
        &__select {
            margin-bottom: 40px;
            @include rp(pm) {
                margin-bottom: 28px;
            }
            display: flex;
            padding-left: 48px;
            @include rp(pm) {
                padding-left: 32px;
            }
        }
        &__seclectSearch {
            width: 350px;
            margin-right: 32px;
            @include rp(tx){
                margin-right: 24px;
            }
            &--input {
                padding: 0px 8px;
                border: none;
                width: 85%;
                height: 40px;
                @include rp(tx){
                    width: 80%;
                    height: 30px;
                }
                box-shadow: 0px 3px 6px rgba(#000, 0.16);
            }
            &--button {
                margin-left: -8px;
                width: 10%;
                height: 40px;
                @include rp(tx){
                    width: 15%;
                    height: 30px;
                }
                border: none;
                background: map-get($color, main);
            }
        }
        &__selectYear {
            margin-right: 32px;
            @include rp(tx){
                margin-right: 24px;
            }
            #cardYear {
                height: 40px;
                @include rp(tx){
                    height: 30px;
                }
                padding: 0 8px;
                &.button {
                    color: map-get($color, main);
                }
            }
        }
        &__selectSort {
            #cardSort {
                height: 40px;
                @include rp(tx){
                    height: 30px;
                }
                padding: 0 8px;
            }
        }
        &__showProduct {
            padding-left: 40px;
            @include rp(pm) {
                padding-left: 32px;
            }
            .box {
                padding: 0px 8px;
                margin-bottom: 40px;
                @include rp(pm) {
                    margin-bottom: 28px;
                }
                height: 300px;
                @include rp(pm) {
                    height: 250px;
                }
                @include rp(tx) {
                    height: 240px;
                }
                @include rp(tm) {
                    height: 235px;
                }
                width: 200px;
                &__img {
                    height: 150px;
                    @include rp(pm) {
                        height: 115px;
                    }
                }
                &__content {
                    background: map-get($color, mainBg);
                    padding: 18px;
                    @include rp(pm) {
                        padding: 14px;
                    }
                    position: relative;
                    &::after {
                        position: absolute;
                        content: "";
                        top: 100%;
                        left: 0%;
                        width: 100%;
                        height: 2px;
                        background: map-get($color, main);
                    }
                    &:hover {
                        background: map-get($color, boxOverlay);
                    }
                }
                &__title {
                    margin-bottom: 20px;
                    @include rp(pm) {
                        margin-bottom: 16px;
                    }
                    & > * {
                        @include size(s16);
                        color: map-get($color, base-2);
                    }
                }
                &__priceDiscount {
                    margin-bottom: 20px;
                    @include rp(pm) {
                        margin-bottom: 16px;
                    }
                    & > * {
                        @include size(s14);
                        color: map-get($color, base-2);
                    }
                }
                &__more {
                    display: flex;
                    justify-content: space-between;

                    svg {
                        margin-left: 4px;
                    }
                }
            }
        }
    }
}
