.product-card-2 {
    padding-bottom: 80px;
    background: #f6f6f6;
    &__wrapper {
        display: flex;
        justify-content: center;
        width: 100%;
        @include rp(ts){
            display: flex;
            flex-direction: column;
        }
    }
    &__coreSale {
        padding-left: 25px;
       width: 40%;
       @include rp(ts){
        width: 80%;
    }
       &--title{
           padding-bottom: 24px;
           &>*{
               @include size(s34);
               color: map-get($color , title );
           }
       }
       &--input{
           padding-bottom: 24px;
           @include rp(ts){
            padding-bottom: 0px;
        }
        input{
            padding: 0px 8px;
            width: 200px;
            @include rp(ts){
                width: 80%;
            }
            height: 50px;
            border: none;
            box-shadow: 0ox 3px 6px rgba( #000000, 0.16);
        }
    }
    &--button{
       padding: 24px;
       background: #fff;
       border: none;
       box-shadow: 0px 3px 6px rgba( #000000,  0.16);
       &:hover{
        cursor: pointer;
    }
    @include rp(ts){
        margin: 16px 0px;
    }
    }
    }
    &__TotalProvisional {
        width: 50%;
        @include rp(ts){
            width: 100%;
        }
        background: #fff;
        
        &--priceProvisional {
            width: 100%;
            display: flex;
            padding: 24px;
            border: 1px solid #212121;
            .title{
                width: 50%;
                &>*{
                    @include size(s24);
                    color: map-get($color, title );
                }
            }
            .price{
                width: 50%;
                display: flex;
                justify-content: flex-end;
                &>*{
                    @include size(s24);
                    color: map-get($color, title );
                }
            }
        }
        &--ship{
            border: 1px solid #212121;
            padding: 24px;
            .title{
                padding-bottom: 8px;
                &>*{
                    @include size(s24);
                    color: map-get($color, title );
                }
            }
        }
        &--priceTotal {
            border: 1px solid #212121;
            padding: 24px;
            width: 100%;
            display: flex;
            
            .title{
                width: 50%;
                &>*{
                    @include size(s24);
                    color: map-get($color, title );
                }
            }
            .priceTotal{
                display: flex;
                justify-content: flex-end;
                width: 50%;
                &>*{
                    @include size(s24);
                    color: map-get($color, priceDiscount );
                }
            }
        }
        &--button{
            width: 100%;
            margin-top: 16px;
            padding: 24px;
            border: none;
            background: map-get($color, main );
            box-shadow: 0px 3px 6px rgba( #000000,  0.16);
            &>*{
                @include size(s24);
                color: map-get($color, mainBg );
            }
            &:hover{
                cursor: pointer;
            }
        }
    }
}
