.product-details-1 {
    background: map-get($color, mainBg-1);
    padding-top: 20px;
    padding-bottom: 20px;

    &__section {
        width: 100%;
    }
    .content {
        
        display: flex;
        @include rp(tl) {
            
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        &__slide {
            
            &--top {
                // height: 400px;
                
                .content__img {
                    width: 100%;
                    img{
                        object-fit: cover;
                        
                        height: 350px;
                        @include rp(tl){
                            width:  100%;
                            height: 300px;
                        }
                        @include rp(tl){
                            height: 250px;
                        }
                    }
                    // height: 400px;
                   
                }
                

               
            }
            &--thumb {
                padding-top: 20px;
                // height: 120px;
                .content__img {
                    width: 100%;
                    
                    img{
                        box-shadow: 0px 3px 6px rgba(#0000, 0.16);
                        border-radius: 10px;
                        height: 70px;
                    }
                }
            }
        }
        &__infor {
            // width: 50%;
            padding: 0 80px;
            @include rp(pm) {
                padding: 0 80px;
            }
            @include rp(tx) {
                padding: 0 60px;
            }
            @include rp(tl) {
                
                padding: 0 0px;
            }
        }
        &__title {
            display: flex;
            justify-content: center;
            @include rp(tl) {
                display: flex;
                justify-content: center;
                margin-top: 16px;
                // @include rp(tl) {
                //     display: flex;
                //     justify-content: flex-start;
                //     margin-top: 16px;
                // }
            }
            & > * {
                @include size(s34);
                @include rp(pm) {
                    font-size: 3.2rem;
                }
                color: map-get($color, title);
                @include bold;
                @include uppercase;
            }
        }
        &__productCore {
            display: flex;
            justify-content: space-between;

            margin-top: 80px;
            @include rp(pm) {
                margin-top: 70px;
            }
            @include rp(tx) {
                margin-top: 50px;
            }
            @include rp(tl) {
                display: flex;
                justify-content: flex-start;
                margin-top: 30px;
            }
            margin-bottom: 24px;
            @include rp(tx) {
                margin-bottom: 20px;
            }
            @include rp(tl) {
                margin-bottom: 16px;
            }
            &--title {
                @include rp(tl) {
                    width: 50%;
                }
                & > * {
                    @include size(s20);
                    color: map-get($color, title);
                }
            }
            &--value {
                & > * {
                    @include size(s16);
                    color: map-get($color, base);
                }
            }
        }
        &__productYear {
            display: flex;
            justify-content: space-between;
            margin-bottom: 24px;
            @include rp(tx) {
                margin-bottom: 20px;
            }
            @include rp(tl) {
                margin-bottom: 16px;
            }
            @include rp(tl) {
                display: flex;
                justify-content: flex-start;
            }
            &--title {
                @include rp(tl) {
                    width: 50%;
                }
                & > * {
                    @include size(s20);
                    color: map-get($color, title);
                }
            }
            &--value {
                & > * {
                    @include size(s16);
                    color: map-get($color, base);
                }
            }
        }
        &__productOrigin {
            display: flex;
            justify-content: space-between;
            margin-bottom: 24px;
            @include rp(tx) {
                margin-bottom: 20px;
            }
            @include rp(tl) {
                margin-bottom: 16px;
            }
            @include rp(tl) {
                display: flex;
                justify-content: flex-start;
            }
            &--title {
                @include rp(tl) {
                    width: 50%;
                }
                & > * {
                    @include size(s20);
                    color: map-get($color, title);
                }
            }
            &--value {
                & > * {
                    @include size(s16);
                    color: map-get($color, base);
                }
            }
        }
        &__productPrice {
            display: flex;
            justify-content: space-between;
            margin-bottom: 24px;
            @include rp(tl) {
                display: flex;
                justify-content: flex-start;
            }
            @include rp(tx) {
                margin-bottom: 20px;
            }
            @include rp(tl) {
                margin-bottom: 16px;
            }
            &--title {
                @include rp(tl) {
                    width: 50%;
                }
                & > * {
                    @include size(s20);
                    color: map-get($color, title);
                }
            }
            &--value {
                & > * {
                    @include size(s16);
                    color: map-get($color, base);
                }
            }
        }
        &__button {
            margin-bottom: 80px;
            @include rp(pm) {
                margin-bottom: 70px;
            }
            @include rp(tx) {
                margin-bottom: 50px;
            }
            display: flex;
            justify-content: flex-end;
            @include rp(tl) {
                display: flex;
                justify-content: flex-start;
            }
            @include rp(ts) {
                display: flex;
                justify-content: center;
            }

            // @include rp(mx) {
            //     display: flex;
            //     justify-content: space-between;
            // }
            &--buy {
                width: 180px;
                padding: 24px 4px;
                @include rp(pm) {
                    width: 160px;
                }
                @include rp(tl) {
                    width: 140px;
                    margin-right: 40px;
                    // height: 50px;
                    padding: 16px 4px;
                }
                @include rp(ts) {
                    width: 100%;
                    margin-right: 0px;
                }
                border: none;
                background: map-get($color, buttonBg);
                box-shadow: 0px 3px 6px rgba(#0000, 0.16);
                & > * {
                    @include size(s18);
                    color: map-get($color, main);
                }
                &:hover {
                    background: map-get($color, main);
                    & > * {
                        color: map-get($color, mainBg);
                    }
                }
            }
            &--card {
                width: 180px;
                height: 70px;
                @include rp(pm) {
                    width: 160px;
                }
                border: none;
                background: map-get($color, buttonBg);
                box-shadow: 0px 3px 6px rgba(#0000, 0.16);
                & > * {
                    @include size(s18);
                    color: map-get($color, main);
                }
                &:hover {
                    background: map-get($color, main);
                    & > * {
                        color: map-get($color, mainBg);
                    }
                }
            }
        }
    }
}
