.index4 {
    .index-news {  
            position: relative;
            background: url("../assets/images/page/index/Image.png");
            background-repeat: no-repeat;
            height: auto;
        &__content {
            width: 100%;
            display: flex;
            flex-direction: column;
            @include spaceIndex4(s60, margin-top);
            @include spaceIndex4(s60, margin-bottom);
        }
        &__title {
            @include spaceIndex4(s40, padding-bottom);
            display: flex;
            justify-content: center;
            &>*{
                @include size(s40);
                @include bold;
                color: map-get($color, mainBg );
            }
        }
        &__box {
           
        }
        .news-box {
            
            position: relative;
            background: map-get($color, mainBg);
            &__img {
                @include blockIndex(imgIndex4Width, width);
                @include blockIndex(imgIndex4Height, height);
            }
            &__content {
                @include spaceIndex4(s24, padding-left);
                @include spaceIndex4(s24, padding-right);
                @include spaceIndex4(s24, margin-top);
            }
            &__time {
                @include spaceIndex4(s24, margin-bottom);
                &>*{
                    @include size(s16);
                    color: map-get($color, main-2 );
                }
            }
            &__title {
                @include spaceIndex4(s32, margin-bottom);
                &>*{
                    @include size(s24);
                    color: map-get($color, main-2 );
                }
            }
            &__desc {
                @include spaceIndex4(s24, margin-bottom);
                &>*{
                    @include size(s16);
                    line-height: 1.4;
                    color: map-get($color, base-2 );
                }
            }
            &__more{
                @include spaceIndex4(s24, padding-bottom);
            }
            
        }
        .swiper-pagination {
            position: absolute;
            bottom: 2%;
            left: 50%;
            transform: translateX(-50%);
            &>*{
                margin-right: 8px;
                height: 16px;
                width: 16px;
                background:map-get($color, main )
            }
         }
    }
}
