////////////////////////////////////////////////////////////
//////////////// BANNER ////////////////////////////////////

.banner {
  max-width: 1920px;
  margin: auto;

  // @include heightBanner;

  position: relative;

  &-img {
  }

  &__content {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &__title {
    padding-bottom: 8px;
    @include rp(pm){
      padding-bottom: 4px;
    }
    &>* {
      @include size(s80);
      @include rp(pl){
        font-size: 7.6rem;
      }
      @include rp(ps){
        font-size: 7.2rem;
      }
      @include rp(tx){
        font-size: 6.8rem;
      }
      @include rp(tm){
        font-size: 6.4rem;
      }
      @include rp(mx){
        font-size:  5rem;
      }
      @include rp(mm){
        font-size: 4.6rem;
      }
      @include family--primary;
      @include uppercase;
      color: map-get($color, title );
    }
  }
  &__titleSub {
    
    padding-bottom: 24px;
    @include rp(pm){
      padding-bottom: 20px;
    }
    @include rp(ps){
      padding-bottom: 16px;
    }
    &>* {
      @include size(s45);
      @include rp(pl){
        font-size: 4rem;
      }
      @include rp(ps){
        font-size: 3.6rem;
      }
      @include rp(tx){
        font-size: 3.2rem;
      }
      @include rp(tm){
        font-size: 2.8rem;
      }
      @include rp(mx){
        font-size: 2.4rem;
      }
      @include rp(mm){
        font-size: 2rem;
      }
      @include family--primary;
      @include uppercase;
      @include bold;
      color: map-get($color, titleSub );
    }
  }
  &__shopNow {
    padding: 24px 56px;
    @include rp(pl){
      padding: 20px 48px;
    }
    @include rp(tx){
      padding: 20px 40px;
    }
    @include rp(mx){
      padding: 16px 28px;
    }
    border-radius: 20px;
    background: map-get($color, main-2 );
    &>* {
      @include size(s18);
      @include rp(pl){
        font-size: 1.6rem;
      }
      @include rp(mx){
        font-size: 1.4rem;
      }
      @include uppercase;
      @include bold;
      color: map-get($color, mainBg );
    }
  }
  .swiper-pagination{
    position: absolute;
    left: 50%;
    bottom: 5%;
    transform: translateX(-50%);
    &>*{
      width: 16px;
      height: 16px;
       margin: 0px 4px;
       background: map-get($color, main-2);
       @include rp(mx){
        width: 8px;
      height: 8px;
    }
    }
  }
}

